export let arrListId = ['1','2','3']
export const [firstListId, secondListId, thirdListId] = arrListId


export function addNewId () {
    const max = (Math.max(...arrListId) + 1).toString();
    arrListId = [...arrListId, max]
}

export function deleteId (idTodo) {
    arrListId = arrListId.filter(i => i !== idTodo)
}
