import {ALL, IN_PROGRESS} from "./filter";

export function watcherData(sort, store, props, task) {

    const {idTodo} = props

    if (sort.value === ALL) {
        task.value = store.getters.all(idTodo)
    } else if (sort.value === IN_PROGRESS) {
        task.value = store.getters.inProgress(idTodo)
    }
}